.main {
    margin-top: 120px;
    padding-bottom: 120px;
}

.title {
    color: #FFF;
    font-family: Inter, sans-serif;
    font-size: 32px;
    font-style: normal;
    font-weight: 600;
    line-height: 110%; /* 35.2px */
    text-transform: uppercase;
}

.content {
    display: grid;
    grid-template-columns: 1fr;
    max-width: 992px;
    width: 100%;
    grid-row-gap: 12px;
    margin-top: 24px;
}

.footer {
    margin-top: 32px;
}

.footer_title {
    color: #FFF;
    font-family: Inter, sans-serif;
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: 150%; /* 30px */
}

.btn {
    color: #FFF;
    font-family: Inter, sans-serif;
    font-size: 18px;
    font-style: normal;
    font-weight: 600;
    line-height: 150%; /* 27px */
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 13px;
    border: 1px solid #FFF;
    height: 43px;
    max-width: 260px;
    width: 100%;
    margin-top: 16px;
    cursor: pointer;
    transition: .3s all;
}

.btn:hover {
    border: 1px solid #d9d7d7;
    color: #d9d7d7;
}

@media screen and (max-width: 700px) {
    .main {
        margin-top: 72px;
        padding: 0 16px;
        padding-bottom: 96px;
    }

    .title {
        font-size: 24px;
        font-style: normal;
        font-weight: 600;
        line-height: 110%;
    }

    .footer_title {
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: 150%;
    }

    .btn {
        width: 100%;
        max-width: 100%;
        font-size: 18px;
        font-style: normal;
        font-weight: 600;
        line-height: 150%;
    }
}
