.main {
    margin-top: 104px;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 600px;
    background: #050321;
    position: relative;
    width: calc(100% + 40px);
    margin-left: -20px;
    overflow: hidden;
    transition: .3s all;
}

.title {
    color: #FFF;
    text-align: center;
    font-family: Inter, sans-serif;
    max-width: 921px;
    width: 100%;
    font-size: 32px;
    font-style: normal;
    font-weight: 600;
    line-height: 140%; /* 44.8px */
    text-transform: uppercase;
    position: relative;
    z-index: 10;
}

.icons {
    display: flex;
    align-items: flex-end;
    position: absolute;
}

.icons {
    position: absolute;
    right: -9px;
    bottom: -34px;
    z-index: 3;
}

.iconm {
    position: absolute;
    right: 6px;
    bottom: -36px;
    z-index: 2;
}

.iconb {
    position: absolute;
    right: 147px;
    bottom: -2px;
    z-index: 1;
}

@media screen and (max-width: 700px) {
    .main {
        margin-top: 48px;
        width: calc(100% + 8px);
        margin-left: -4px;
        min-height: 527px;
        height: auto;
        align-items: flex-start;
        padding-top: 48px;
    }

    .title {
        max-width: 310px;
        font-size: 18px;
        font-style: normal;
        font-weight: 600;
        line-height: 140%;
    }

    .icons img {
        height: 215px;
        width: auto;
    }

    .iconm img {
        height: 240px;
        width: auto;
    }

    .iconb img {
        height: 282px;
        width: auto;
    }

    .iconm {
        position: absolute;
        right: -50px;
        bottom: -20px;
        z-index: 2;
    }

    .icons {
        position: absolute;
        right: 2px;
        bottom: -17px;
        z-index: 3;
    }

    .iconb {
        position: absolute;
        right: 40px;
        bottom: -2px;
        z-index: 1;
    }
}
