.content {
    min-height: 100%;
    display: flex;
    position: relative;
    z-index: 1;
    overflow: hidden;
}

.content_1 {
    padding: 56px 0 0 56px;
}

.content_3 {
    padding: 24px 24px 24px 56px;
    justify-content: space-between;
}

.content_2 {
    padding: 56px;
    justify-content: space-between;
}

.title {
    color: #FFF;
    font-family: Inter, sans-serif;
    font-size: 32px;
    font-style: normal;
    font-weight: 600;
    line-height: 120%; /* 38.4px */
    text-transform: uppercase;
    max-width: 360px;
}

.sub_title {
    color: #FFF;
    font-family: Inter, sans-serif;
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: 140%;
    margin-top: 16px;
}

.sub_title1 {
    max-width: 415px;
    width: 100%;
}

.btn_1 {
    color: #FFF;
    font-family: Inter, sans-serif;
    font-size: 16px;
    font-style: normal;
    font-weight: 600;
    line-height: 110%; /* 17.6px */
    text-transform: uppercase;
    padding: 12px 16px;
    width: fit-content;
    border-radius: 49px;
}

.content_left1 {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.content_left1_bottom {
    margin-bottom: 56px;
}

.img {
    position: absolute;
    right: 0;
    bottom: 0px;
}

.sub_title2 {
    text-align: left;
    max-width: 380px;
}

.sub_title2 span {
    text-decoration: underline;
}

.tooltip {
    display: flex;
    align-items: center;
    justify-content: center;
    color: #FFF;
    font-family: Inter, sans-serif;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 140%; /* 19.6px */

}

.content_left2 {
    max-width: 468px;
    width: 100%;
    min-height: 100%;
}

.content_left1_bottom2 {
    margin-bottom: 0;
}

.img_box2 {
    width: 100%;
}

.img_box2_1 {
    position: absolute;
    z-index: 1;
}

.img_box2_2 {
    z-index: 2;
    position: absolute;
    bottom: 0;
    left: 320px;
}

.sub_title3 {
    max-width: 360px;
}

.content_left1_bottom3 {
    margin-bottom: 32px;
}

.iframe {
    border-radius: 16px;
    overflow: hidden;
    min-height: 100%;
    max-width: 822px;
    width: 100%;
}

.img_box2_11, .img_box2_22 {
    display: none;
}

@media screen and (max-width: 700px) {
    .content_left1 {
        display: flex;
        flex-direction: column-reverse;
    }

    .content {
        display: flex;
        flex-direction: column;
    }

    .img {
        position: relative;
        margin-top: 40px;
        right: 0px;
        width: 407px;
        height: auto;
    }

    .content_1 {
        padding: 32px 16px 0 16px;
    }

    .title {
        font-size: 24px;
        font-style: normal;
        font-weight: 600;
        line-height: 120%;
        max-width: 280px;
    }

    .content_left1_bottom {
        margin-bottom: 24px;
    }

    .sub_title1 {
        max-width: 90%;
    }

    .sub_title {
        margin-top: 8px;
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: 140%;
    }

    .btn_1 {
        font-size: 14px;
        font-style: normal;
        font-weight: 600;
        line-height: 110%;
    }

    .content_2 {
        flex-direction: column-reverse;
        padding: 32px 16px 0 16px;
    }

    .img_box2_1, .img_box2_2 {
        display: none;
    }

    .img_box2_11, .img_box2_22 {
        display: block;
        position: absolute;
    }

    .img_box2 {
        position: relative;
        height: 220px;
        margin-top: 55px;
    }

    .img_box2_11 {
        left: -16px;
    }

    .img_box2_22 {
        right: -16px;
        bottom: 0px;
    }

    .content_3 {
        padding: 32px 16px 0 16px;
    }

    .iframe {
        border-radius: 16px;
        overflow: hidden;
        min-height: 180px;
        max-width: 100%;
        width: 100%;
        min-width: calc(100% + 32px);
        margin-left: -16px;
    }

    .sub_title3 {
        max-width: 85%;
    }
}
