.accordion_wrapper {
    width: 100%;
    position: relative;
}

.accordion_header {
    width: 100%;
    display: flex;
    align-items: center;
    cursor: pointer;
    user-select: none;
    padding: 10px 16px;
    border-radius: 13px;
    background: rgba(255, 255, 255, 0.06);
    transition: .3s all;
    justify-content: space-between;
}

.accordion_header svg {
    transition: .3s all;
}

.header_icon svg {
    transform: rotate(180deg);
}

.accordion_header p {
    color: #FFF;
    font-family: Inter, sans-serif;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: 150%;
}

.accordion_inner_content {
    margin-top: 12px;
    color: #FFF;
    font-family: Inter, sans-serif;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 150%; /* 24px */
    max-width: 965px;
}

.accordion_inner_content a {
    text-decoration: underline;
}

.accordion_header_title {
    color: #4F4F4F;
    font-family: Inter, sans-serif;
    font-size: 18px;
    font-style: normal;
    font-weight: 300;
    line-height: normal;
}

.accordion {
    width: 100%;
}

.accordion_content {
    overflow: hidden;
    height: 0;
}

@media screen and (max-width: 700px) {
    .accordion_inner_content {
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: 150%;
    }

    .accordion_header p {
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: 150%;
        max-width: 85%;
    }
}
