.main {
    margin-top: 120px;
    display: flex;
}

.main_left {
    max-width: 431px;
    width: 100%;
}

.main_left_title {
    color: #FFF;
    font-family: Inter, sans-serif;
    font-size: 32px;
    font-style: normal;
    font-weight: 600;
    line-height: 110%; /* 35.2px */
    text-transform: uppercase;
}

.main_left_sub_title {
    margin-top: 16px;
    color: #FFF;
    font-family: Inter, sans-serif;
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: 150%; /* 30px */
}

.main_right {
    margin-left: 61px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 16px;
    grid-row-gap: 12px;
    width: 100%;
}

.item {
    border-radius: 12px;
    background: rgba(255, 255, 255, 0.06);
    padding: 32px;
    width: 100%;
    position: relative;
    overflow: hidden;
}

.item_title {
    color: #FFF;
    font-family: Inter, sans-serif;
    font-size: 22px;
    font-style: normal;
    font-weight: 700;
    line-height: 140%; /* 30.8px */
}

.item_sub_title {
    color: #FFF;
    font-family: Inter, sans-serif;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 140%; /* 22.4px */
    margin-top: 8px;
    max-width: 280px;
}

.number {
    position: absolute;
    /*right: 0;*/
    color: rgba(255, 255, 255, 0.20);
    font-family: Inter, sans-serif;
    font-size: 231.14px;
    font-style: normal;
    font-weight: 900;
    line-height: 140%; /* 323.597px */
    right: -50px;
    bottom: -77px;
}

.number_1 {
    right: -27px;
}

.last {
    border-radius: 12px;
    background: #1162FF;
    padding: 32px;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    cursor: pointer;
    transition: .3s all;
}

.last_item_title {
    color: #FFF;
    font-family: Inter, sans-serif;
    font-size: 22px;
    font-style: normal;
    font-weight: 700;
    line-height: 140%; /* 30.8px */
    max-width: 260px;
}

.last:hover {
    background: #1259E2;
}

.arrow {
    width: 100%;
    display: flex;
    justify-content: flex-end;
}

@media screen and (max-width: 700px) {
    .main {
        margin-top: 80px;
        flex-direction: column;
    }

    .main_left_title {
        font-size: 24px;
        font-style: normal;
        font-weight: 600;
        line-height: 110%;
    }

    .main_left_sub_title {
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: 150%;
    }

    .main_left {
        padding: 0 16px;
    }

    .main_right {
        margin-left: 0px;
        margin-top: 24px;
        display: grid;
        grid-template-columns: 1fr;
        grid-row-gap: 4px;
    }

    .item {
        padding: 20px;
    }

    .item_title {
        font-size: 18px;
        font-style: normal;
        font-weight: 700;
        line-height: 140%;
    }

    .item_sub_title {
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: 140%;
        max-width: 230px;
    }

    .last {
        padding: 20px;
        min-height: 130px;
    }

    .last_item_title {
        font-size: 18px;
        font-style: normal;
        font-weight: 700;
        line-height: 140%;
    }

    .number {
        font-size: 189.05px;
        font-style: normal;
        font-weight: 900;
        line-height: 140%;
        right: -40.836px;
        bottom: -67px;
    }

    .number_1 {
        right: -20px;
        bottom: -80px;
    }
}
