.main {
    margin-top: 110px;
    padding: 56px;
    border-radius: 40px;
    background: #2B2A37;
    margin-bottom: 192px;
}

.header_title {
    color: #FFF;
    font-family: Inter, sans-serif;
    font-size: 40px;
    font-style: normal;
    font-weight: 600;
    line-height: 110%; /* 44px */
    text-transform: uppercase;
}

.header_description {
    display: flex;
    align-items: center;
    column-gap: 32px;
    margin-top: 24px;
}

.header_item {
    display: flex;
    align-items: center;
}

.header_item_circle {
    padding: 12px 16px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #FFF;
    font-family: Inter, sans-serif;
    font-size: 16px;
    font-style: normal;
    font-weight: 800;
    text-transform: uppercase;
    border-radius: 49px;
    background: #1162FF;
}

.header_item_title {
    color: #FFF;
    font-family: Inter, sans-serif;
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    margin-left: 8px;
}


.content {
    margin-top: 45px;
    display: grid;
    grid-template-columns: 1fr;
    grid-row-gap: 56px;
}

.box_title {
    color: #FFF;
    font-family: Inter, sans-serif;
    font-size: 32px;
    font-style: normal;
    font-weight: 600;
    line-height: 130%; /* 41.6px */
}

.box_sub_title {
    color: #FFF;
    font-family: Inter, sans-serif;
    margin-top: 8px;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 140%; /* 22.4px */
}

.box_sub_title1 {
    max-width: 656px;
}

.items {
    margin-top: 24px;
}

.box_sub_title2 {
    max-width: 790px;
}

.show_more {
    border-radius: 13px;
    background: rgba(255, 255, 255, 0.10);
    width: 100%;
    height: 44px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #FFF;
    font-family: Inter, sans-serif;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: 130%; /* 20.8px */
    margin-top: 16px;
    cursor: pointer;
    transition: .3s all;
}

.show_more:hover {
    background: rgba(255, 255, 255, 0.20);
}

.box_sub_title3 {
    max-width: 683px;
}

@media screen and (max-width: 700px) {
    .main {
        border-radius: 24px;
        margin-top: 72px;
        margin-bottom: 72px;
        padding: 40px 16px;
    }

    .header_title {
        font-size: 24px;
        font-style: normal;
        font-weight: 600;
        line-height: 110%;
    }

    .header_description {
        margin-top: 16px;
        column-gap: 20px;
        row-gap: 20px;
        flex-wrap: wrap;
    }

    .header_item_circle {
        padding: 12px 12px;
        font-size: 14px;
        font-style: normal;
        font-weight: 800;
        line-height: 110%;
        height: 32px;
    }

    .header_item_title {
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: 150%;
        white-space: nowrap;
    }

    .content {
        margin-top: 40px;
        grid-row-gap: 48px;
    }

    .box_title {
        font-size: 20px;
        font-style: normal;
        font-weight: 600;
        line-height: 130%;
    }

    .box_sub_title {
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: 140%;
    }

    .show_more {
        margin-top: 24px;
    }
}
