.header {
    position: relative;
}

.title {
    color: #FFF;
    font-family: Inter, sans-serif;
    font-size: 36px;
    font-style: normal;
    font-weight: 600;
    line-height: 120%; /* 43.2px */
    text-transform: uppercase;
}

.sub_title {
    margin-top: 12px;
    color: #FFF;
    font-family: Inter, sans-serif;
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: 150%; /* 30px */
}

.close {
    border-radius: 64px;
    background: rgba(255, 255, 255, 0.10);
    display: flex;
    align-items: center;
    justify-content: center;
    height: 56px;
    width: 56px;
    cursor: pointer;
    transition: .3s all;
    position: absolute;
    right: -32px;
    top: -32px;
}

.close:hover {
    background: rgba(255, 255, 255, 0.20);
}

.content {
    overflow: auto;
    margin-top: 32px;
    display: flex;
    grid-row-gap: 32px;
    flex-direction: column;
    height: 100%;
}

.content::-webkit-scrollbar {
    width: 16px;
    height: 2px;
}

.content::-webkit-scrollbar-track {
    background: rgba(255, 255, 255, 0.20);
    border-radius: 59px;
    /*-webkit-box-shadow: inset 0 0 10px rgba(0,0,0,0.3);*/
}

.content::-webkit-scrollbar-thumb {
    background-color: #FFF;
    border-radius: 40px;
    width: 12px;
}

.modal_title {
    max-width: 702px;
    width: 100%;
    color: #FFF;
    font-family: Inter, sans-serif;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 150%; /* 24px */
    margin-top: 4px;
}

.ul {
    margin: 8px 0;
    max-width: 702px;
}

.ul p {
    position: relative;
    color: #FFF;
    font-family: Inter, sans-serif;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 150%; /* 24px */
    margin-left: 18px;
}

.ul p:before {
    content: '';
    position: absolute;
    height: 6px;
    width: 6px;
    background: #fff;
    border-radius: 50%;
    top: 12px;
    left: -16px;
}

.item {
    padding-right: 20px;
    /*display: flex;*/
    /*align-items: flex-start;*/
}

.number {
    height: 40px;
    width: 40px;
    border-radius: 50%;
    background: #1162FF;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #FFF;
    font-family: Inter, sans-serif;
    font-size: 16px;
    font-style: normal;
    font-weight: 800;
    text-transform: uppercase;
}

.item_top {
    display: flex;
    align-items: center;
}

.item_title {
    color: #FFF;
    font-family: Inter, sans-serif;
    font-size: 22px;
    font-style: normal;
    font-weight: 700;
    line-height: 140%; /* 30.8px */
    margin-left: 16px;
}

.item_bottom {
    margin-left: 56px;
}
