.main {
    border-radius: 24px 24px 0px 0px;
    background: #111;
    padding: 24px 56px;
    width: calc(100% + 40px);
    margin-left: -20px;
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
}

.main_left, .main_right {
    display: flex;
    align-items: flex-end;
}

.main_left p {
    color: #FFF;
    font-family: Inter, sans-serif;
    margin-left: 24px;
    font-size: 14px;
    font-style: italic;
    font-weight: 500;
    line-height: 110%; /* 15.4px */
    text-transform: uppercase;
    margin-bottom: 5px;
}

.title {
    margin-bottom: 5px;
    color: #FFF;
    font-family: Inter, sans-serif;
    margin-right: 16px;
    font-size: 14px;
    font-style: italic;
    font-weight: 500;
    line-height: 110%; /* 15.4px */
    text-transform: uppercase;
}

@media screen and (max-width: 700px) {
    .main {
        width: calc(100% + 8px);
        margin-left: -4px;
        padding: 16px 16px 32px 16px;
        flex-direction: column;
    }

    .main_left svg {
        width: 130px;
        height: auto;
    }

    .main_left{
        justify-content: flex-start;
        width: 100%;
    }

    .main_left p {
        margin-left: 16px;
        margin-bottom: 3px;
        font-size: 12px;
    }

    .main_right{
        flex-direction: column;
        margin-top: 30px;
    }

    .title{
        margin-right: 0;
        font-size: 12px;
        margin-bottom: 8px;
    }

    .main_right svg {
        width: 100px;
        height: auto;
    }
}
