.box {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 24px 24px 24px 56px;
    border-radius: 32px;
    background: rgba(255, 255, 255, 0.06);
}

.img {
    height: 534px;
    width: 534px;
}

.cheaps {
    display: flex;
    align-items: center;
    column-gap: 6px;
}

.cheap {
    padding: 0px 16px;
    height: 42px;
    color: #FFF;
    font-family: Inter, sans-serif;
    font-size: 16px;
    font-style: normal;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 600;
    /*line-height: 110%; !* 17.6px *!*/
    text-transform: uppercase;
    border-radius: 49px;
    background: #1E1D2B;
}

.title {
    color: #FFF;
    font-family: Inter, sans-serif;
    font-size: 40px;
    font-style: normal;
    font-weight: 600;
    line-height: 120%; /* 48px */
    text-transform: uppercase;
    margin-top: 24px;
}

.sub_title {
    color: #FFF;
    font-family: Inter, sans-serif;
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: 140%; /* 28px */
    max-width: 461px;
    width: 100%;
    margin-top: 24px;
}

.btn {
    margin-top: 40px;
    max-width: 267px;
    height: 62px;
    color: #FFF;
    text-align: center;
    font-family: Inter, sans-serif;
    font-size: 20px;
    font-style: normal;
    font-weight: 600;
    line-height: 110%; /* 22px */
    text-transform: uppercase;
    border-radius: 12px;
}

@media screen and (max-width: 700px) {
    .box {
        padding: 24px 16px 16px 16px;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    .cheaps {
        flex-wrap: wrap;
        gap: 6px;
    }

    .cheap {
        font-size: 14px;
        height: 39px;
    }

    .title {
        margin-top: 16px;
        font-size: 26px;
    }

    .sub_title {
        margin-top: 10px;
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: 140%;
    }

    .btn {
        max-width: 100%;
        margin-top: 16px;
        font-size: 20px;
        font-style: normal;
        font-weight: 600;
        line-height: 110%;
        display: none !important;
    }

    .img{
        width: 100%;
        height: auto;
        margin-top: 20px;
    }
}
