.command {
    margin-top: 140px;
}

.header {
    display: flex;
    align-items: flex-end;
}

.header p {
    color: #FFF;
    font-family: Inter, sans-serif;
    font-size: 14px;
    font-style: italic;
    font-weight: 500;
    line-height: 110%; /* 15.4px */
    text-transform: uppercase;
    margin-left: 12px;
}

.content {
    display: flex;
    margin-top: 16px;
}

.title {
    color: #FFF;
    font-family: Inter, sans-serif;
    font-size: 36px;
    font-style: normal;
    font-weight: 600;
    line-height: 120%; /* 43.2px */
    text-transform: uppercase;
    max-width: 520px;
    width: 100%;
}

.sub_title {
    margin-left: 32px;
    color: #FFF;
    font-family: Inter, sans-serif;
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: 150%; /* 30px */
    max-width: 767px;
    /*width: 100%;*/
}

@media screen and (max-width: 700px) {
    .command {
        margin-top: 100px;
        padding: 0 16px;
    }

    .content {
        flex-direction: column;
    }

    .sub_title {
        margin-left: 0;
    }

    .header svg {
        width: 130px;
        height: auto;
    }

    .header p {
        margin-left: 12px;
        margin-bottom: 3px;
        font-size: 14px;
        font-style: italic;
        font-weight: 500;
        line-height: 110%;
    }

    .title{
        font-size: 24px;
        font-style: normal;
        font-weight: 600;
        line-height: 120%;
    }
    .sub_title{
        margin-top: 16px;
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: 150%;
    }
}
