.main {
    margin-top: 156px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.title {
    color: #FFF;
    font-family: Inter, sans-serif;
    font-size: 32px;
    font-style: normal;
    font-weight: 600;
    text-transform: uppercase;
    max-width: 527px;
    width: 100%;
    line-height: 120%;
}

.sub_title {
    color: #FFF;
    font-family: Inter, sans-serif;
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    max-width: 527px;
    width: 100%;
    margin-top: 24px;
    line-height: 140%;
}

.icon {
    margin-right: 25px;
}

.description_box {
    margin-top: -115px;
}

@media screen and (max-width: 700px) {
    .description_box{
        margin-top: 0;
    }
    .main {
        margin-top: 72px;
        flex-direction: column;
        padding: 0 16px;
    }

    .title{
        font-size: 24px;
        font-style: normal;
        font-weight: 600;
        line-height: 120%;
    }

    .sub_title{
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: 140%;
    }

    .icon{
        margin-right: 0;
        display: flex;
        align-self: center;
        justify-content: center;
        margin-top: 40px;
    }

    .icon svg {
        width: 293px;
        height: auto;
    }
}
