.item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-top: 1px solid #4A4A4A;
    padding: 7px 0;
}

.title {
    color: #FFF;
    font-family: Inter, sans-serif;
    font-size: 20px;
    font-style: normal;
    font-weight: 600;
}

.number {
    color: #FFF;
    font-family: Inter, sans-serif;
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    text-transform: uppercase;
    border-radius: 49px;
    background: #1E1D2B;
    padding: 11px 16px;
    width: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
}

@media screen and (max-width: 700px) {
    .title{
        font-size: 16px;
        font-style: normal;
        font-weight: 600;
        line-height: 130%;
        max-width: 70%;
    }

    .number{
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: 110%;
    }
}
