.list {
    margin-top: 55px;
    display: flex;
    column-gap: 72px;
    padding: 0 35px;
}

.circle {
    width: 12px;
    height: 12px;
    background: #fff;
    border-radius: 50%;
}

.title {
    color: #FFF;
    font-family: Inter, sans-serif;
    margin-left: 12px;
    font-size: 20px;
    font-style: normal;
    font-weight: 700;
    line-height: 140%; /* 28px */
}

.sub_title {
    margin-left: 24px;
    color: #FFF;
    font-family: Inter, sans-serif;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 140%; /* 22.4px */
    margin-top: 12px;
}

.item_top {
    display: flex;
    align-items: center;
}

.item1 {
    max-width: 214px;
    width: 100%;
}

.item2 {
    max-width: 256px;
    width: 100%;
}

.item3 {
    max-width: 315px;
    width: 100%;
}

.item4 {
    max-width: 257px;
    width: 100%;
}

@media screen and (max-width: 700px) {
    .list {
        margin-top: 32px;
        flex-direction: column;
        row-gap: 16px;
        padding: 0 15px;
    }

    .title {
        font-size: 18px;
        font-style: normal;
        font-weight: 700;
        line-height: 140%;
    }

    .sub_title {
        margin-top: 8px;
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: 140%;
    }

    .item1 {
        max-width: 80%;
        width: 100%;
    }

    .item2 {
        max-width: 85%;
        width: 100%;
    }

    .item3 {
        max-width: 85%;
        width: 100%;
    }

    .item4 {
        max-width: 80%;
        width: 100%;
    }
}
