.landing {
    padding: 0 20px;
    background: #1E1D2B;
    min-height: 100vh;
    min-height: 100 dvh;
}

.btn {
    border-radius: 12px;
    background: #1162FF;
    box-shadow: 0px 0px 20px 0px rgba(17, 98, 255, 0.30);
    color: #FFF;
    text-align: center;
    font-family: Inter, sans-serif;
    font-size: 20px;
    font-style: normal;
    font-weight: 600;
    line-height: 110%; /* 22px */
    text-transform: uppercase;
    padding: 20px 40px;
    width: fit-content;

}

.btn_box {
    width: fit-content;
    position: fixed;
    right: 32px;
    bottom: 32px;
    z-index: 10;
    transition: .3s all;
}

@media screen and (max-width: 700px) {
    .landing {
        padding: 0 4px;
    }

    .btn_box{
        width: 100%;
        padding: 16px;
        bottom: 0;
        background: #111;
        right: 0;
    }

    .btn{
        width: 100%;
        height: 56px;
    }
}
