.header {
    display: flex;
    align-items: flex-end;
    padding: 24px 24px;
}

.title {
    color: #FFF;
    font-family: Inter, sans-serif;
    font-size: 14px;
    font-style: italic;
    font-weight: 500;
    line-height: 110%; /* 15.4px */
    text-transform: uppercase;
    margin-left: 24px;
    margin-bottom: 5px;
}

@media screen and (max-width: 700px) {
    .header {
        padding: 16px;
    }

    .title {
        margin-left: 16px;
        font-size: 12px;
        margin-bottom: 2px;
    }

    .header svg {
        width: 130px;
        height: auto;
    }
}
